<template>
	<div class="personal-container" @click="headerUserDropboxhide">
		<div class="personal-header-box2">
			<div class="personal-header flex_between">
				<div class="header-logo">
					<img src="../../images/login_logo.png" alt="" @click="goToHome('homepage')">
				</div>
				<div class="header_menu flex_center header_center">
					<div v-for="item in router_list" class="">
						<router-link :to="{name: item.component}">
							{{ item.text }}
						</router-link>
					</div>
				</div>
				<div class="header-user">
					<div @click.stop="changeHeaderUserDropboxShow" class="flex_center">
						<img @error="getimg" :src="userUrl">
						<span>{{userName}}</span>
					</div>
					<div class="header-user-dropbox" :class="{'header-user-dropbox-show':headerUserDropboxShow}">
						<div class="header-user-dropbox-item" @click="goToHome('personalInfo')">账户管理</div>
						<div class="header-user-dropbox-item" @click="goToHome('loginSafe')">登录安全</div>
						<div class="header-user-dropbox-item" @click="toAlertEwm"><span v-if="toShow"></span> 关注公众号</div>
						<div class="header-user-dropbox-item" @click="logout">退出登录</div>
					</div>
				</div>
			</div>
		</div>
		<div class="personal-body" >
			<div class="personal-content" :class="{'personal-content-showtip':tipspanShow}">
				<div class="personal_content_tipspan" :class="{'personal_content_tipspan_full':fulltipspan}" v-if="tipspanShow">
					<img src="../../images/tipspan_icon.png" alt="" class="personal_content_tipspan_icon">
					<span class="personal_content_tipspan_text">投后管理到期提醒倒计时：提醒时间1月1号，文案内容找产品。</span>
					<img src="../../images/tipspan_close.png" @click="hideTipspan" alt="" class="personal_content_tipspan_close">
				</div>
				<router-view ></router-view>
			</div>

		</div>
		<!--扫码关注公众号-->
		<el-dialog title="扫码关注微信公众号" :visible.sync="showEwm" width="400px" center :append-to-body='true' :before-close="toCancleConfirm" class="wxgzh">
			<div class="wxgzh_img_box">
				<img :src="ewmSrc" alt="">
			</div>
		</el-dialog>
	</div>
</template>
<script>
	import { apiLogout, apiAutoLogin, bindQr, isBindQr, getRouterListApi } from '../../config/api'
	import api from '../../config/url.js'
	import { mapState, mapMutations } from 'vuex'
	import { setStore, getStore, removeStore, dialogConfirm } from '../../config/mUtils'
	export default {
		data() {
			return {
				firstClickId: '',
				secondClickId: '',
				thirdClickId: '',
				isChildClick: '', //左边二级导航
				defaultOpeneds: ['/home/administrativeManageHomepage'], //左侧导航默认打开项
				defaultActive: '', //左侧导航默认选中项
				leftNavList: '', //左侧导航栏数据
				headerUserDropboxShow: false, //头部用户信息下拉框
				// username: '',
				userImgurl: '',
				selectId: null,
				selectName: '',
				tipspanShow: false,
				fulltipspan: false,
				showEwm: false,
				toShow: false,
				ewmSrc: '',
				router_list: [{
						name: 'homepage',
						label: '首页'
					},
					{
						name: 'investManageHome',
						label: '投资管理'
					},
					{
						name: 'fundManageHome',
						label: '基金管理'
					},
					{
						name: 'administrativeManageHome',
						label: '行政管理'
					},
					{
						name: 'systemManageHome',
						label: '系统管理'
					},
				]
			};
		},
		watch: {
//			$route(to, from) {
//				// console.log(from);
//				if(from.name == 'investorReportView') {
//					setStore('needGetData', 'true');
//				} else {
//					setStore('needGetData', 'false');
//				}
//
//				let name = to.name;
//				let fromname = from.name;;
//				let tonamelist = [];
//				let keyPath = [];
//				let oneMenuId = '';
//				if(name == 'homepage') {
//					this.fulltipspan = true
//				} else {
//					this.fulltipspan = false
//				}
//				for(let item of this.leftNavList) {
//					if(item.child != undefined) {
//						if(item.child.length > 0) {
//							for(let itemChidren of item.child) {
//								if(itemChidren.name == name) {
//									if(item.id != undefined && item.id != null) {
//										oneMenuId = item.id.toString();
//									}
//									keyPath.push(oneMenuId);
//								}
//							}
//						}
//					}
//				}
//				if(oneMenuId != '') {
//					keyPath.push(to.path);
//				} else if(name == 'homepage' || name == 'homePage' || name == 'personalInfo' || name == 'loginSafe') {
//					this.goHome();
//				}
//				if(fromname == 'investProjectManageHomepage') {
//					tonamelist = ['investFolderManage', 'applyList', 'investedCompanyContactManage', 'investedCompanyAccountManage', 'investedPublishNews', 'investedCompanyEventloglist'];
//					if(tonamelist.indexOf(name) != -1) {
//						this.firstClickId = 1;
//						this.secondClickId = '';
//						this.defaultActive = '/home/investProjectManageList';
//						this.selectName = 'investProjectManageHomepage';
//					}
//				}
//			}
		},
		computed: {
			...mapState([
				'userInfo', 'userUrl', 'userName'
			])
		},
		created() {
			if(this.$route.name == 'homepage') {
				this.fulltipspan = true
			} else {
				this.fulltipspan = false
			}
			if(!this.userInfo || this.userInfo != '') {
				if(!getStore('htcx_userInfo')) {
					this.$router.push('/login');
				} else {
					this.RECORD_USERINFO(JSON.parse(getStore('htcx_userInfo')));
					this.RECORD_USERURL(getStore('htcx_userUrl'));
					this.RECORD_USERNAME(getStore('htcx_userName'));
				}
			}
			this.leftNavList = this.userInfo.menu;
			if(this.userUrl == '') {
				this.RECORD_USERURL('../static/images/header_userimg.png');
			}

			let toContinue = true;
			this.leftNavList.forEach(item1 => {
				if(item1.name == this.$route.name && item1.child == undefined) {
					this.firstClickId = item1.id;
					this.secondClickId = '';
					this.thirdClickId = ''
					toContinue = false;
				}
			});
			if(toContinue) {
				let jg_select_id1 = window.sessionStorage.getItem('jg_select_id1');
				let jg_select_id2 = window.sessionStorage.getItem('jg_select_id2');
				let jg_default_activ = window.sessionStorage.getItem('jg_default_activ');
				let jg_select_name = window.sessionStorage.getItem('jg_select_name');
				this.firstClickId = jg_select_id1;
				this.secondClickId = jg_select_id2;
				this.defaultActive = jg_default_activ;
				this.selectName = jg_select_name;
			}
			this.getRouterList()
			this.getIsBindQr();
		},
		methods: {
			...mapMutations([
				'RECORD_USERINFO', 'RECORD_USERURL', 'RECORD_USERNAME'
			]),
			// 获取导航路由数据
			getRouterList(){
				getRouterListApi({
					specMenuId: 0
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.router_list = res.data.payload;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			//隐藏顶部通知
			hideTipspan() {
				this.tipspanShow = false
			},
			//打开菜单时触发
			handleOpen(key, keyPath) {
				this.leftNavList.forEach(item1 => {
					// console.log( item1.id == key)
					if(item1.id == key) {
						this.firstClickId = key;
						this.secondClickId = '';
						this.thirdClickId = ''
						if(item1.child == undefined) {
							this.$router.push({
								name: item1.name
							})
						}
					} else {
						if(item1.child != undefined) {
							item1.child.forEach(item2 => {
								if(item2.id == key) {
									this.firstClickId = item1.id
									this.secondClickId = item2.id
									this.thirdClickId = ''
								} else {
									if(item2.child != undefined) {
										item2.child.forEach(item3 => {
											if(item3.id == key) {
												this.firstClickId = item1.id
												this.secondClickId = item2.id
												this.thirdClickId = item3.id
											}
										});
									}
								}
							});
						}
					}
				});
			},
			//关闭菜单时触发
			handleClose(key, keyPath) {
				this.leftNavList.forEach(item1 => {
					if(item1.id == key) {
						if(item1.child != undefined) {
							this.firstClickId = '';
							this.secondClickId = '';
							this.thirdClickId = '';
						} else {
							this.$router.push({
								name: item1.name
							})
						}
					} else {
						if(item1.child != undefined) {
							item1.child.forEach(item2 => {
								if(item2.id == key) {
									this.secondClickId = '';
									this.thirdClickId = '';
								} else {
									if(item2.child != undefined) {
										item2.child.forEach(item3 => {
											if(item3.id == key) {
												this.thirdClickId = ''
											}
										});
									}
								}
							});
						}
					}
				});
			},
			//点击某一行时触发
			handleSelect(key, keyPath) {
				this.defaultActive = key;
				this.selectName = this.defaultActive.split('/')[2];
				let length = keyPath.length;
				if(length == 2) {
					if(this.secondClickId != null) {
						this.secondClickId = this.secondClickId.toString();
					}
					this.$refs.nav.close(this.secondClickId);

					this.secondClickId = '';
					window.sessionStorage.setItem('jg_select_id1', keyPath[0]);
					window.sessionStorage.removeItem('jg_select_id2');
				} else if(length == 3) {
					window.sessionStorage.setItem('jg_select_id1', keyPath[0]);
					window.sessionStorage.setItem('jg_select_id2', keyPath[1]);
				}
				window.sessionStorage.setItem('jg_select_name', this.selectName);
				window.sessionStorage.setItem('jg_default_activ', this.defaultActive);
			},
			//退出登录
			logout() {
				// removeStore("htcx_webtoken"); //存储本次token
				// removeStore("htcx_autologin"); //下次是否自动登录
				// removeStore("htcx_uid"); //uid
				// removeStore("htcx_userInfo"); //uid
				let version = getStore('htcx_version');
				window.localStorage.clear();
				window.sessionStorage.clear();
				setStore('htcx_version', version);
				api.resetServer();
				this.$router.push({
					path: '/login'
				})
			},
			//点击页面其他区域隐藏右侧下拉
			headerUserDropboxhide() {
				this.headerUserDropboxShow = false;
				// this.$refs.componentref.DropboxShow222();

			},
			//顶部导航栏下拉框打开关闭
			changeHeaderUserDropboxShow() {
				this.headerUserDropboxShow = !this.headerUserDropboxShow;
			},
			goToHome(name) {
				this.$router.push({
					name: name
				})
			},
			goHome() {
				if(this.firstClickId != null) {
					this.firstClickId = this.firstClickId.toString();
				}
				this.firstClickId = this.firstClickId.toString();
				this.$refs.nav.close(this.firstClickId);
				this.defaultActive = '';
				this.firstClickId = '';
				this.secondClickId = '';
				this.thirdClickId = '';
				this.selectName = ''
				window.sessionStorage.clear();
			},
			getimg() {
				apiAutoLogin().then(res => {
					if(res.data.msgNo == 1) {
						setStore("htcx_userUrl", res.data.payload);
						this.RECORD_USERURL(res.data.payload); //用户信息存全局
						//this.$router.push({ path: '/home/homepage'})
					}
				});
			},
			toAlertEwm() {
				bindQr().then(res => {
					if(res.data.msgNo == 0) {
						this.ewmSrc = res.data.payload.url;
						this.showEwm = true;
					}
				});
			},
			toCancleConfirm() {
				this.showEwm = false;
				this.getIsBindQr();
			},
			getIsBindQr() {
				isBindQr().then(res => {
					if(res.data.msgNo == 0) {
						let data = res.data.payload;
						if(data.subscribe == 1) {
							this.toShow = false;
						} else {
							this.toShow = true;
						}
					}
				});
			}
		}
	};
</script>
<style lang="scss" scoped>
	@import "../../style/mixin.scss";
	.cursor {
		cursor: pointer;
	}
	
	.el-menu {
		position: relative; // left: 1px;
		width: 225px;
	}
	
	.slide-icon {
		width: 26px;
		height: 26px;
		overflow: hidden;
		display: inline-block
	}
	
	.slide-shape {
		width: 12px;
		position: absolute;
		top: 50%;
		right: 20px;
		margin-top: -1px;
	}
	
	.white {
		color: #FFFFFF;
	} // .header-logo span{
	//   color: #476995;
	// }
	.red_circle {
		position: relative;
		left: -5px;
		top: -9px;
		display: inline-block;
		width: 6px;
		height: 6px;
		border-radius: 50%;
		background: #FF7676;
	}
	
	.red_circle1 {
		left: 0px;
		top: -8px;
	}
	
	.wxgzh {
		.wxgzh_img_box {
			text-align: center;
			img {
				width: 330px;
				height: 330px;
			}
		}
	}
	/*盛景改版*/
	
	.header_center div {
		padding: 10px 42px;
		font-size: 18px;
		color: #545454;
	}
	
	.header_center a {
		color: #545454;
	}
	
	.header_center a:hover {
		color: #6292F4;
	}
	
	.router-link-active {
		color: #6292F4 !important;
	}
</style>


